<script>
import user from './user'
import Activate from './Activate.vue'
import customerService from './customerService'

export default {
	name: "FixedMenu",
	components: {
		user,
		Activate,
		customerService,
	},
	props: {},
	data() {
		return {
			activeMenu: ''
		};
	},
	computed: {},
	watch: {},
	created() {},
	mounted() {},
	methods: {
		changeMenu(menu) {
			if (this.activeMenu == menu) {
				this.activeMenu = '';
				return;
			}
			this.activeMenu = menu;
		},
		backTop() {
			window.scrollTo({
				top: 0,
				behavior: "smooth",
			});
		},
	},
};
</script>
