<script>
import Banner from "./Banner";
import ScrollDevelopment from './ScrollDevelopment'
import FixedMenuVue from '@/components/FixedMenu/FixedMenu.vue';

export default {
	name: "AboutUs",
	components: {
		Banner,
		ScrollDevelopment,
		FixedMenuVue,
	},
	props: {},
	data() {
		return {};
	},
	computed: {},
	watch: {},
	created() {},
	mounted() {},
	methods: {
		handleScroll(e) {
			const scrollContainer = this.$refs.scrollContainer;
			const scrollbar = this.$refs.scrollbar;
			const scrollDirection = e.deltaY > 0 ? 1 : -1;
			scrollContainer.scrollLeft += scrollDirection * 50;

			// 获取scrollContainer元素内部类名为historyItem元素的个数
			const historyItemCount = scrollContainer.querySelectorAll(
				".historyItem"
			).length;
			// 获取historyItem的宽度
			const historyItemWidth = scrollContainer.querySelector(
				".historyItem"
			).offsetWidth;
			// 算出要滚到到底的距离
			let AllDistance = (historyItemCount - 3) * historyItemWidth;

			// 算出scrollContainer.scrollLeft占AllDistance的比例
			let scrollPercent = parseInt((scrollContainer.scrollLeft / AllDistance) * 100);

			// 算出innerBar的宽度占scrollbar宽度的百分比（因为位移到底的时候还要减去一个自身的宽度）
			let innerBarWidthPercent = parseInt(this.innerBar.offsetWidth / scrollbar.offsetWidth * 100)
			let allPercent = 100 - innerBarWidthPercent;

			if (scrollPercent > allPercent) {
				scrollPercent = allPercent;
			}

			this.innerBar.style.left = scrollPercent + "%";
		

			e.preventDefault();
		},
		initScrollBar() {
			this.innerBar = this.$refs.innerBar;
			const scrollContainer = this.$refs.scrollContainer;

			// 获取scrollContainer元素内部类名为historyItem元素的个数
			const historyItemCount = scrollContainer.querySelectorAll(
				".historyItem"
			).length;

			this.innerBar.style.width = 1 / historyItemCount * 100 + "%";
		},
	},
};
</script>
